:root {
  --cubic: cubic-bezier(0.4, 0, 0.2, 1);
  --bg-color: rgb(238, 169, 229);
  --primary-color: #da3e60;
  --bg-input-color: #fffaf0;
  --text-color: #212121;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

.container {
  margin: 0 auto;
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
}

//  input-form

.input-section {
  margin: 0 auto;
  padding: 30px 0;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: var(--primary-color);
  border-bottom: 1px solid var(--bg-input-color);
}

.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.input-value {
  width: 350px;
  height: 32px;
  padding-left: 30px;
  border-radius: 15px;

  background-color: var(--bg-input-color);
  border: 2px solid var(--bg-color);

  transition: background-color 250ms var(--cubic);
  transition: border 250ms var(--cubic);

  &:hover,
  &:focus {
    background-color: var(--bg-color);
    border: 2px solid var(--bg-input-color);
  }
}

.btn-search {
  margin-left: 15px;
  padding: 3px 1px;
  width: 34px;
  height: 32px;
  display: inline-block;
  cursor: pointer;
  justify-content: center;

  border: 2px solid var(--bg-color);
  background-color: var(--bg-input-color);

  transition: background-color 250ms var(--cubic);
  transition: border 250ms var(--cubic);

  &:hover,
  &:focus {
    background-color: var(--bg-color);
    border: 2px solid var(--bg-input-color);
  }
}

//  gallery

img {
  display: block;
}

.gallery {
  display: grid;
  width: 1200px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  margin: 30px 0;
}

.gallery-card {
  position: relative;
  background-color: var(--bg-input-color);
  padding: 10px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.photo-card {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform 250ms var(--cubic);

  &:hover {
    transform: scale(1.03);
  }
}

.stats {
  display: flex;
  justify-content: center;
  padding: 15px;
}

.stats-item {
  &:not(:last-child) {
    margin-right: 25px;
  }
}

.material-icons {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
}

// button

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;

  width: 150px;
  height: 30px;
  border-radius: 10px;

  cursor: pointer;
  justify-content: center;

  border: 2px solid var(--primary-color);
  background-color: var(--bg-input-color);

  transition: background-color 250ms var(--cubic);
  transition: border 250ms var(--cubic);

  &:hover,
  &:focus {
    background-color: var(--primary-color);
    border: 2px solid var(--bg-input-color);
  }
}

.button.is-hidden {
  display: none;
}

.button > .spinner.is-hidden {
  display: none;
}
